    todetail(id) {
      this.$router.push({
        path: "specDetail",
        query: {
          id
        }
      })
    },
    del(index){
      Dialog.confirm({
        title: '提示',
        message: '是否删除此规格？'
      }).then(() => {
        this.list.splice(index,1)
        // on confirm
      }).catch(() => {
        // on cancel
      });
    },
    eidt(index){
      this.spceName = this.list[index].name
      this.temp ={
        index,
        query:this.list[index]
      } 
      this.showModel = true
    },
    // 显示弹出层
    addSpce() {
      this.showModel = !this.showModel;
    },
    // 添加规格参数
    addChild(index){
      if(this.childName == ''){
        layer.msg('请输入规格参数');
        return
      }
      // 添加
      this.list[index].child.push({
        id:this.list[index].child.length+1,
        name:this.childName,
        select:0
      })
    },
    // 新增规格
    sure() {
      if(this.spceName == '' ){
        layer.msg('请输入规格名称');
        return
      }
      // 编辑
      if(this.temp){
        this.list[this.temp.index].name = this.spceName
        this.temp = ''
      }else{
        let temp = {
           id:this.list.length + 1,
           name:this.spceName,
           select:false,
           checkBox:false,
           child:[]
        }
        this.list.push(temp)
      }
      this.spceName = ''
      this.showModel = false
    },
    // 确认选择
    closeAll(){
      this.$emit("close",this.list)
    },
  }
