        margin: 0 auto;
        margin-top: 0.39rem;
        min-width: 1.2rem;

        ._icon {
          width: 0.2rem;
          margin-right: 0.1rem;
        }
      }
    }

  }
  .F{
    .sd_btn {
      position: fixed;
      bottom: 0.4rem;
      left: 0px;
    }
  }
  .sd_flexd {
    position: fixed;
    bottom: 0rem;
    left: 0px;
    width: 100%;
    font-size: 0.28rem;
    border: none;
    color: #222;
    background: #fff;

    ._icon {
      width: 0.38rem !important;
      height: 0.38rem !important;
    }
  }
}
